@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
/*@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap");*/

* {
    margin: 0;
    padding: 0;
}

.doughnut-chart-container {
    width: 500px;
    height: 600px;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

#chartjs-tooltip {
    text-align: center;
    border-radius: 50%;
    /*width: 210px;*/
    /*height: 210px;*/
    /*margin: 15px;*/
    border: solid 2px black;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    color: white;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

#chartjs-tooltip * {
    padding: 0;
    margin: 0;

}

#chartjs-tooltip h3 {
    font: 25px "SF Pro Display";
    margin: 25px;
    /*margin-bottom: 25px;*/
    /*margin-top: 30px;*/
}

#chartjs-tooltip p {
    margin-bottom: 15px;
}

.tooltip-percent {
    font: 20px "SF Pro Text";

}

.tooltip-amount {
    font: 15px "SF Pro Text"
}

.slider-menu {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row;
            flex-flow: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin-top: 20px;
}

.slider-menu-item {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: rgba(0, 0, 0, 0.25);
    margin: 0 4px;
}

.slider-menu-item-active {
    width: 8px;
    height: 8px;
    background: white;
}

.slider-container {
    width: 300px;
    height: 500px;
    background: linear-gradient(to right bottom, #9e66c6, #6027e1);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: stretch;
            align-items: stretch;
    position: relative
}

.slider-container * {
    text-align: center;
}

.slider-image-container {
    /*background: rgba(255, 255, 255, 0.25);*/
    /*background: #5da3f2;*/
    width: 160px;
    height: 160px;
    border-radius: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 32px 0;
}

.slider-image-bg {
    position: absolute;
    background: rgba(255, 255, 255, 0.25);
    width: 160px;
    height: 160px;
    border-radius: 50%;
    right: 50%;
    margin-right: -80px;
    top: 60px;
    z-index: 49;
}

.slider-image {
    width: 130px;
    height: auto;
    z-index: 50;
    transition: opacity 0.4s cubic-bezier(.25, .8, .25, 1), -webkit-transform 0.4s cubic-bezier(.25, .8, .25, 1);
    transition: transform 0.4s cubic-bezier(.25, .8, .25, 1), opacity 0.4s cubic-bezier(.25, .8, .25, 1);
    transition: transform 0.4s cubic-bezier(.25, .8, .25, 1), opacity 0.4s cubic-bezier(.25, .8, .25, 1), -webkit-transform 0.4s cubic-bezier(.25, .8, .25, 1);
    /*overflow: hidden;*/
}

.slider-image-hidden {
    opacity: 0;
    -webkit-transform: translateY(-150px);
            transform: translateY(-150px);
}

.slider-content {
    transition: opacity 0.4s cubic-bezier(.25, .8, .25, 1),
    -webkit-transform 0.4s cubic-bezier(.25, .8, .25, 1);
    transition: transform 0.4s cubic-bezier(.25, .8, .25, 1),
    opacity 0.4s cubic-bezier(.25, .8, .25, 1);
    transition: transform 0.4s cubic-bezier(.25, .8, .25, 1),
    opacity 0.4s cubic-bezier(.25, .8, .25, 1),
    -webkit-transform 0.4s cubic-bezier(.25, .8, .25, 1);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    -webkit-align-items: center;
            align-items: center;
}

.slider-content h3 {
    color: white;
    font-size: 20px;
    line-height: 1.4em;
    text-transform: uppercase;
    /*letter-spacing: 0em;*/
    font-family: "Lato", sans-serif;
    /*margin: 0 0 15px 0;*/
}

.slider-content a {
    color: white;
}

.slider-rating {
    font-family: "Lato", sans-serif;
    font-weight: 400;
    color: white;
    line-height: 1.6em;
    font-size: 20px;
    margin: 10px 0;
}

.slider-description {
    font-family: "Lato", sans-serif;
    font-weight: 300;
    color: rgba(255, 255, 255, 0.8);
    line-height: 1.6em;
    font-size: 15px;
}


.slider-arrow-container {
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    width: 40px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.slider-arrow-container {
    color: rgba(255, 255, 255, 0.25);
    font-size: 24px;
    cursor: pointer;
    transition: color 0.4s cubic-bezier(.25, .8, .25, 1), opacity 0.4s cubic-bezier(.25, .8, .25, 1), -webkit-transform 0.4s cubic-bezier(.25, .8, .25, 1);
    transition: transform 0.4s cubic-bezier(.25, .8, .25, 1), color 0.4s cubic-bezier(.25, .8, .25, 1), opacity 0.4s cubic-bezier(.25, .8, .25, 1);
    transition: transform 0.4s cubic-bezier(.25, .8, .25, 1), color 0.4s cubic-bezier(.25, .8, .25, 1), opacity 0.4s cubic-bezier(.25, .8, .25, 1), -webkit-transform 0.4s cubic-bezier(.25, .8, .25, 1);
    z-index: 100;
}

.slider-arrow-container:hover {
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
    color: white;
}

.slider-main-container {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: stretch;
            align-items: stretch;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.slide-hidden-right {
    opacity: 0;
    position: absolute;
    -webkit-transform: translateX(100px);
            transform: translateX(100px);
}

.slide-hidden-left {
    opacity: 0;
    position: absolute;
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
}

